import { Canvas } from "@react-three/fiber";
import { useGLTF, PresentationControls, Stats } from "@react-three/drei";

export default function App() {
  const { scene } = useGLTF(
    "https://uploads.codesandbox.io/uploads/user/e04e4cfe-5bf8-49d0-9918-ce65be9ca4e2/iR-o-3dweb_Homepage_v7.glb"
  );

  return (
    <Canvas flat dpr={[1, 2]} camera={{ fov: 8, position: [15, 8, 15] }}>
      <ambientLight />
      <group position={[0, -0.6, 0]}>
        <PresentationControls
          global
          snap={true}
          polar={[0, 0]}
          azimuth={[-Math.PI / 4, Math.PI / 4]}
        >
          <primitive object={scene} />
        </PresentationControls>
      </group>
      <Stats />
    </Canvas>
  );
}
